import React, { Component } from 'react';
import './bootstrap.min.css';
import './DistributionTable.css';
import { ORGANIZATIONS } from './StaticData';

class DistributionTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: props.title,
            selected: ORGANIZATIONS[0],
            count: 0,
            options: ORGANIZATIONS
        };

        this.orgSelector = (
            <select className="col-md-6" onChange={this.onSelectChange.bind(this)}>
                {ORGANIZATIONS.map(org => <option key={org} value={org}>{org}</option>)}
            </select>
        );

        this.countInput = 
            <input onChange={this.onCountChange.bind(this)} type="text" className="form-control col-md-4" placeholder="名額"/>;
        const addRowButtonClasses = "btn btn-outline-secondary col-md-2";
        this.addRowButton = (
            <button onClick={this.addRow.bind(this)} className={addRowButtonClasses} type="button">
                增加單位
            </button>
        );

        this.tbodyRef = React.createRef();
    }

    onSelectChange(event) {
        this.setState({ selected: event.target.value });
    }

    addRow() {
        console.log(this.myRef);
        const tbody = this.tbodyRef.current;
        let row = tbody.insertRow(tbody.rows.length);
        let cell1 = row.insertCell(0);
        let cell2 = row.insertCell(1);
        cell1.innerHTML = this.state.selected;
        cell2.innerHTML = this.state.count;
    }

    onCountChange(event) {
        this.setState({ count: event.target.value});
    }

    render() {
        return (
            <div className="table-responsive col-md-12">
                <table className="table table-bordered table-hover table-sm">
                    <thead>
                        <tr>
                            <th className="unit">分發單位</th>
                            <th className="count">剩餘名額</th>
                        </tr>
                    </thead>
                    <tbody ref={this.tbodyRef}>
                        <tr id="inputRow">
                            <td colSpan="2">
                                <div className="input-group mb-3">
                                    {this.addRowButton}
                                    {this.orgSelector}
                                    {this.countInput}
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        );
    }
}

export default DistributionTable;